class ModalServicesSelesai extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: "",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "total",
          text: "Total Rp",
          formatter: (cell, row, rowIndex) => {
            return this.getTotal(rowIndex);
          },
          footer: (cell, row, rowIndex) => {
            // console.log(row);
            // return this.state.total
            return this.getTotalAll();
          },
        },
      ],
    };
  }

  getTotalAll() {
    let hasil = JSON.parse(localStorage.getItem("total_rp_services")) || [];
    let total = 0;
    hasil.forEach((item, index) => {
      total = total + item.jml;
    });
    return parseInt(total).toLocaleString("kr-KO");
  }
  getTotal(rowIndex) {
    // console.log());
    let cek = JSON.parse(localStorage.getItem("total_rp_services")) || [];
    let hasil = {
      jml: parseInt(
        this.props.detail_barang[rowIndex].ongkos *
          this.props.detail_barang[rowIndex].jumlah
      ),
    };
    cek.push(hasil);
    localStorage.setItem("total_rp_services", JSON.stringify(cek));
    return parseInt(
      this.props.detail_barang[rowIndex].ongkos *
        this.props.detail_barang[rowIndex].jumlah
    ).toLocaleString("kr-KO");
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-6">
            <Field
              name="no_faktur_services"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              name="nama_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Nama Customer"
              placeholder="Masukan Nama Cistomer"
            />
          </div>
          <div className="col-6">
            <Field
              name="alamat_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Nama Customer"
              placeholder="Masukan Alamat Cistomer"
            />
          </div>
          <div className="col-6">
          <Field
              name="no_hp"
              component={ReanderField}
              type="number"
              label="No Hp"
              readOnly={true}
              placeholder="Masukan No Hp"
            />
          </div>
          {/* <div className="col-6">
            <Field
              name="tgl_selesai"
              component={ReanderField}
              type="date"
              label="Tanggal Selesai"
              placeholder="Masukan Tanggal Selesai"
            />
          </div> */}
          <div className="col-12">
            <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={this.props.detail_barang}
              columns={this.state.columns}
              pagination="off"
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          <div className="col-12">
            <button type="submit"
              disabled={this.props.isLoadingServicesSelesai}
              className="btn btn-primary btn-block">
                 {this.props.isLoadingServicesSelesai ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan Data
                </>
              ) : (
                "Service Selesai"
              )}
            </button>
          </div>
        </div>
        {/* {this.props.isLoadingServicesSelesai ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
      </form>
    );
  }
}

ModalServicesSelesai = reduxForm({
  form: "ModalServicesSelesai",
    enableReinitialize: true,
    validate: ValidasiServicesSelesai
})(ModalServicesSelesai);
// export default connect(maptostate, null)(ModalServicesSelesai);

export default connect((state) => {
  return {
    detail_barang: state.services.showModalServiceSelesai.detail_barang,
    initialValues: {
      no_faktur_services: state.services.showModalServiceSelesai.no_faktur_service,
      nama_customer: state.services.showModalServiceSelesai.nama_customer,
      alamat_customer: state.services.showModalServiceSelesai.alamat_customer,
      no_hp: state.services.showModalServiceSelesai.no_hp,
      tgl_selesai : getToday(),
    },
  };
})(ModalServicesSelesai);
