const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
class ModalServicesBatal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date("Y-m-d"),
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: "",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "total",
          text: "Total Rp",
          formatter: (cell, row, rowIndex) => {
            return this.getTotal(rowIndex);
          },
          footer: (cell, row, rowIndex) => {
            // console.log(row);
            // return this.state.total
            return this.getTotalAll();
          },
        },
      ],
    };
  }
  getTotalAll() {
    let hasil =
      JSON.parse(localStorage.getItem("total_rp_service_ambil")) || [];
    let total = 0;
    hasil.forEach((item, index) => {
      total = total + item.jml;
    });
    return parseInt(total).toLocaleString("kr-KO");
  }
  getTotal(rowIndex) {
    // console.log());
    let cek = JSON.parse(localStorage.getItem("total_rp_service_ambil")) || [];
    let hasil = {
      jml: parseInt(
        this.props.detail_barang[rowIndex].ongkos *
          this.props.detail_barang[rowIndex].jumlah
      ),
    };
    cek.push(hasil);
    localStorage.setItem("total_rp_service_ambil", JSON.stringify(cek));
    return parseInt(
      this.props.detail_barang[rowIndex].ongkos *
        this.props.detail_barang[rowIndex].jumlah
    ).toLocaleString("kr-KO");
  }

  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              id="no_faktur_services_ambil"
              name="no_faktur_services"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              name="nama_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Nama Customer"
              placeholder="Masukan Nama Customer"
            />
          </div>
          <div className="col-4">
            <Field
              name="alamat_customer"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Alamat Customer"
              placeholder="Masukan Alamat Customer"
            />
          </div>
          <div className="col-4">
            <Field
              name="no_hp"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="No Hp"
              placeholder="Masukan No Hp"
            />
          </div>

          <div className="col-3">
            <Field
              id="total_bayar"
              name="total_bayar"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              id="total_rp"
              name="total_rp"
              component={ReanderField}
              {...currencyMask}
              type="text"
              label="Total Rp"
              readOnly={true}
            />
          </div>
          <div className="col-3">
            <Field
              name="total_dp"
              component={ReanderField}
              type="tel"
              {...currencyMask}
              label="Total DP"
              readOnly={true}
            />
          </div>
          <div className="col-3">
            <Field
              name="sisa_rp"
              component={ReanderField}
              type="tel"
              {...currencyMask}
              label="Sisa Rp"
              readOnly={true}
            />
          </div>

          <div className="col-3">
            <Field
              defaultValue={this.state.date}
              id="tgl_ambil"
              name="tgl_ambil"
              component={ReanderField}
              type="date"
              label="Tanggal Ambil"
            />
          </div>

          <div className="col-12">
            <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={this.props.detail_barang}
              columns={this.state.columns}
              pagination="off"
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          <div className="col-3">
            <button
              type="button"
              onClick={this.props.back}
              className="btn btn-primary btn-block"
            >
              <i className="fa fa-arrow-left"></i> Kembali
            </button>
          </div>
          <div className="col-9">
            {JSON.parse(localStorage.getItem("sisa_bayar_service_ambil")) ===
            0 ? (
              <button type="submit" className="btn btn-success btn-block">
                Batal Service
              </button>
            ) : (
              <>
                <button type="submit" className="btn btn-success btn-block">
                  Batal Service
                </button>
              </>
            )}
          </div>
        </div>
        <br />
        {/* <ModalGlobal title="Pembayaran" content={<ModalPembayaran />} /> */}
      </form>
    );
  }
}

ModalServicesBatal = reduxForm({
  form: "ModalServicesBatal",
  enableReinitialize: true,
  validate: ValidasiServicesSelesai,
})(ModalServicesBatal);
// export default connect(maptostate, null)(ModalServicesBatal);

export default connect((state) => {
  localStorage.setItem(
    "sisa_bayar_service_ambil",
    parseFloat(state.services.showModalServiceAmbil.total || 0) -
      parseFloat(state.services.showModalServiceAmbil.dp_rp || 0) || 0
  );
  return {
    detail_barang: state.services.showModalServiceAmbil.detail_barang,
    initialValues: {
      sisa_rp:
        parseFloat(state.services.showModalServiceAmbil.total || 0) -
          parseFloat(state.services.showModalServiceAmbil.dp_rp || 0) || 0,
      no_faktur_services:
        state.services.showModalServiceAmbil.no_faktur_service,
      nama_customer: state.services.showModalServiceAmbil.nama_customer,
      alamat_customer: state.services.showModalServiceAmbil.alamat_customer,
      no_hp: state.services.showModalServiceAmbil.no_hp,
      total_dp: state.services.showModalServiceAmbil.dp_rp,
      total_rp: state.services.showModalServiceAmbil.total,
      total_bayar: state.services.showModalServiceAmbil.total,
      tgl_ambil: getToday(),
    },
  };
})(ModalServicesBatal);
